.details img{
    width:100%;
    height:400px
}
.bot{
    height:50px;
    width:100%;
    background:white;
    position:fixed;
    bottom:0px;
}
li{
    display:inline-block;
    margin:8px 5px;
    width:40px;
    height:40px;
}
li:first-child{
    margin-left:18px;
}
#store{
    font-size: 8px;
    margin-top:5px;
    color:lightslategray
}
#service{
    font-size: 8px;
    margin-top:5px;
    color:lightslategray
}
#collect{
    font-size:8px;
    margin-top:5px;
    color:lightslategray
}
#bright{
    width:195px;
    height:40px;
    margin-right:0px;
    position: relative;
    left:-5px;
    top:-10px
}
#bright1{
    display:inline-block;
    width:97px;
    height:25px;
    color:white;
    font-weight: bold;
    padding-top:12px;
    text-align: center;
    background:rgb(255,133,42);
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px
}
#bright2{
    display:inline-block;
    width:97px;
    height:25px;
    color:white;
    font-weight: bold;
    text-align: center;
    padding-top:12px;
    background:rgb(255, 78, 34);
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px
}